import React from 'react'
import AniLink from "gatsby-plugin-transition-link/AniLink"
import { AspectRatio, Box, useToken } from '@chakra-ui/react';
import { withPrefix } from 'gatsby';

import ReBox from '../../components/Box'
import Text from '../../components/Text'
import Button from '../../components/Button'
import Container from '../../components/Container'

import Layout from '../Layout';
import { Media, responsive } from '../../components/ThemeProvider/theme'

const IndexPage = () => {
  const [teal] = useToken('colors', ['custom.teal'])
  return (
    <Layout
      noHeader
      bg="custom.lightBlueBg"
      footerProps={{
        bg: 'custom.lightBlueBg',
        position: 'absolute',
        left: '50%',
        bottom: responsive('1.5rem', '3.75rem'),
        transform: 'translateX(-50%)'
      }}
      logoWidth={responsive('3.25em', '8.625em')}
      headerProps={{
        justifyContent: responsive('center', 'flex-end')
      }}
    >
      <Media lessThan="tablet">
        <AspectRatio ratio={834 / 717}>
          <Box
            as="iframe"
            border="none"
            overflow="hidden"
            src={withPrefix('/banner/mobile')}
            title="mobile banner"
          />
        </AspectRatio>
      </Media>
      <Media greaterThanOrEqual="tablet">
        <AspectRatio ratio={2560 / 1140}>
          <Box
            as="iframe"
            border="none"
            overflow="hidden"
            src={withPrefix('/banner/desktop')}
            title="desktop banner"
          />
        </AspectRatio>
      </Media>
      <ReBox.Relative mt="-5%" zIndex={5}>
        <Container textAlign="center">
          <Text
            fontSize={responsive('1.125em', '1.5em')}
            fontWeight="bold"
            color="custom.darkBlue"
            textAlign="center"
          >
            悶了好久，超想出國玩一波！<br />
            憑直覺決定 6 件生活小事，開門抵達和你最配的旅行地！
          </Text>
          <Button.Yellow
            as={AniLink}
            paintDrip
            hex={teal}
            my={responsive('1.375rem', '1.875rem')}
            to="q/1"
          >進入 OMG 任意門</Button.Yellow>
          <Text
            fontSize={responsive('0.875em', '1.25em')}
            color="custom.textGray"
            textAlign="center"
          >
            完成測驗領取驚喜彩蛋，9/10 前騎乘 GoShare，<br />
            憑發票就抽歐美十大度假勝地雙人來回機票！
          </Text>
        </Container>
      </ReBox.Relative>
    </Layout>
  )
}

export default IndexPage
